import React, { Component } from 'react'
import Footer from '../footer';
import Header from '../header';
// import NewsUpdate from './newsUpdate/newsUpdate';
import TotalAmountElem from './util/totalAmountElem';
import ActiveStakeSlider from './util/activeStakeSlider';
import BigNumber from 'bignumber.js'

import stateStore from '../test/stateStore';
import { Profile, Stake } from './typings';

interface Props {

}

export interface State {
  hasError: boolean,
  //profile
  profile: Profile,
  //token
  symbol?: string,
  analysis: { balanceChange: number },
  // balance: TokenBalance,
  governanceRewards: number,
  //stakes
  stakes: Array<Stake>,//active and inactive stakes 
  rewards: Array<Stake>
}

class Dashboard extends Component<Props, State> {
  constructor(props: Props) {
    super(props)

    this.state = stateStore();

    if (this.state.profile.address) {
      this.state.profile.shortAddress = this.state.profile.address.substring(0, 6) + '...' + this.state.profile.address.substring(this.state.profile.address.length - 5, this.state.profile.address.length)
    }
  }

  // static defaultProps:Props = {
  //   // msg: 'Hello everyone!'
  // }

  // state = {}

  componentDidMount = () => {
    console.log('Dashboard mounted');
  }

  componentDidUpdate = () => {
    console.log('Dashboard did update');
  }

  componentWillUnmount = () => {
    console.log('Dashboard will unmount');
  }

  calculateTotalAmount = (arr: Array<Stake>) => {
    return arr.map(val => new BigNumber(val.amount ?? "0")).reduce((a, b) => a.plus(b))
  }

  render() {
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }

    return (
      <div className="nk-wrap ">
        <Header {...this.state.profile} />
        {/* content @s */}
        <div className="nk-content nk-content-lg nk-content-fluid">
          <div className="container-xl wide-lg">
            <div className="nk-content-inner">
              <div className="nk-content-body">
                <div className="nk-block-head">
                  <div className="nk-block-between-md g-3">
                    <div className="nk-block-head-content">
                      <div className="align-center flex-wrap pb-2 gx-4 gy-3">
                        <div>
                          <h4 className="nk-block-title"><span className="h6">Welcome</span> {this.state.profile.shortAddress}</h4>
                        </div>
                      </div>
                      <div className="nk-block-des">
                        <p>Here is a summary of your token assets.</p>
                      </div>
                    </div>
                    {this.state.stakes &&
                      <div className="nk-block-head-content d-none d-md-block">
                        <div className="nk-slider nk-slider-s1">
                          <div className="slider-init" data-slick="{&quot;dots&quot;: true, &quot;arrows&quot;: false, &quot;fade&quot;: true}">
                            {
                              this.state.stakes?.map(function (stake, i) {
                                return <ActiveStakeSlider key={i} {...stake} />;
                              })
                            }
                          </div>
                          <div className="slider-dots" />
                        </div>{/* .nk-slider */}
                      </div>
                    }
                  </div>{/* .nk-block-between */}
                </div>{/* .nk-block-head */}
                {/*<div className="nk-block">
                    <div className="nk-news card card-bordered">
                      <div className="card-inner">
                        <div className="nk-news-list">
                          <a className="nk-news-item" href="#!">
                            <div className="nk-news-icon">
                              <em className="icon ni ni-card-view" />
                            </div>
                            <div className="nk-news-text">
                              <p>You've seen the news? <span> Check out our social media pages</span></p>
                              <em className="icon ni ni-external" />
                            </div>
                          </a>
                        </div>
                      </div>
                    </div>
                  </div>*/}
                <div className="nk-block">
                  <div className="row gy-gs">
                    <div className="col-md-6 col-lg-4">
                      <div className="nk-wg-card is-dark card card-bordered">
                        <TotalAmountElem amount={this.state.profile.balance} change={this.state.analysis.balanceChange} title="Available Balance" />
                      </div>{/* .card */}
                    </div>
                    <div className="col-md-6 col-lg-4">
                      <div className="nk-wg-card is-s1 card card-bordered">
                        <TotalAmountElem amount={this.calculateTotalAmount(this.state.stakes).toFormat(5)}/*  change={} */ title="Total Staked" />
                      </div> {/* .card */}
                    </div>
                    <div className="col-md-12 col-lg-4">
                      <div className="nk-wg-card is-s3 card card-bordered">
                        <TotalAmountElem amount={this.calculateTotalAmount(this.state.rewards).toFormat(5)}/*  change={change} */ title="Total Rewards" />
                      </div>{/* .card */}
                    </div>
                  </div>{/* .row */}
                </div>{/* .nk-block */}
                <div className="nk-block">
                  <div className="row gy-gs">
                    <div className="col-md-6 col-lg-4">
                      <div className="nk-wg-card card card-bordered h-100">
                        <div className="card-inner h-100">
                          <div className="nk-iv-wg2">
                            <div className="nk-iv-wg2-title">
                              <h6 className="title">Your Balance</h6>
                            </div>
                            <div className="nk-iv-wg2-text">
                              <div className="nk-iv-wg2-amount ui-v2">
                                {this.state.profile.balance} eaZI
                                  {this.state.analysis.balanceChange !== 0 &&
                                  <span className={this.state.analysis.balanceChange > 0 ? "change up" : "change down"}>
                                    <span className="sign" />{this.state.analysis.balanceChange}%</span>}
                              </div>
                              <ul className="nk-iv-wg2-list">
                                <li>
                                  <span className="item-label">Staked Token</span>
                                  <span className="item-value">{this.state.profile.stakedBalance}</span>
                                </li>
                                <li>
                                  <span className="item-label">Available Rewards</span>
                                  <span className="item-value">{this.state.profile.balance}</span>
                                </li>
                                <li>
                                  <span className="item-label">Available Balance</span>
                                  <span className="item-value">{this.state.profile.balance}</span>
                                </li>
                                <li className="total">
                                  <span className="item-label">Total Asset</span>
                                  <span className="item-value">{new BigNumber(this.state.profile.balance).plus(this.state.profile.stakedBalance).toFormat(5)}</span>
                                </li>
                              </ul>
                            </div>
                            <div className="nk-iv-wg2-cta">
                              <button className="btn btn-primary btn-lg btn-block" onClick={() => this.setState({ analysis: { balanceChange: this.state.analysis.balanceChange + 0.2 } })}>
                                Claim Rewards
                              </button>
                              <button className="btn btn-trans btn-block">
                                Acquire Tokens
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-6 col-lg-4">
                      <div className="nk-wg-card card card-bordered h-100">
                        <div className="card-inner h-100">
                          <div className="nk-iv-wg2">
                            <div className="nk-iv-wg2-title">
                              <h6 className="title">
                                Recent Rewards Earned
                                  <em className="icon ni ni-info text-primary" data-toggle="tooltip" data-placement="right" title="Rewards earned within the last 30 days" />
                              </h6>
                            </div>
                            <div className="nk-iv-wg2-text">
                              <div className="nk-iv-wg2-amount ui-v2">
                                {this.calculateTotalAmount(this.state.rewards).toFormat(5)} eaZI
                                  {this.state.analysis.balanceChange !== 0 && <span className={this.state.analysis.balanceChange > 0 ? "change up" : "change down"}><span className="sign" />{this.state.analysis.balanceChange}%</span>}
                              </div>
                              <ul className="nk-iv-wg2-list">
                                <li>
                                  <span className="item-label">Governance rewards</span>
                                  <span className="item-value">75</span>
                                </li>
                                <li>
                                  <span className="item-label">Yield rewards</span>
                                  <span className="item-value">3089</span>
                                </li>
                                <li>
                                  <span className="item-label">PAMM rewards</span>
                                  <span className="item-value">8990</span>
                                </li>
                                <li className="total">
                                  <span className="item-label">Total Rewards</span>
                                  <span className="item-value">12,154</span>
                                </li>
                              </ul>
                            </div>
                            <div className="nk-iv-wg2-cta">
                              <button className="btn btn-primary btn-lg btn-block">Stake Tokens</button>
                              <button className="btn btn-trans btn-block">Unstake Tokens</button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>{/* .col */}
                    <div className="col-md-12 col-lg-4">
                      <div className="nk-wg-card card card-bordered h-100">
                        <div className="card-inner h-100">
                          <div className="nk-iv-wg2">
                            <div className="nk-iv-wg2-title">
                              <h6 className="title">Recent Stakes<em className="icon ni ni-info text-primary" data-toggle="tooltip" data-placement="right" title="Short list of active stakes"></em>
                              </h6>
                            </div>
                            <div className="nk-iv-wg2-text">
                              <div className="nk-iv-wg2-amount ui-v2">19 Active</div>
                              <ul className="nk-iv-wg2-list">
                                {
                                  [
                                    { id: 0, name: 'eaZIPAMM', stakePeriod: '1 mo', expectedPercent: 7.85, expectValue: 1500 },
                                    { id: 1, name: 'eaZIPAMM', stakePeriod: '2 mo', expectedPercent: 16.38, expectValue: 8367 },
                                    { id: 2, name: 'eaZILEND', stakePeriod: '2 Wks', expectedPercent: 3.76, expectValue: 2502 },
                                    { id: 3, name: 'eaZIYIELD', stakePeriod: '7 Days', expectedPercent: 4.76, expectValue: 4520 }
                                  ].map(item => (
                                    <li key={item.id}>
                                      <span className="item-label"><a href="#!">{item.name}</a> <small>- {item.expectedPercent}% for {item.stakePeriod}</small></span>
                                      <span className="item-value">{item.expectValue.toLocaleString()}</span>
                                    </li>
                                  ))
                                }
                              </ul>
                            </div>
                            <div className="nk-iv-wg2-cta">
                              <button className="btn btn-light btn-lg btn-block">See all Stakes</button>
                              <div className="cta-extra">Analyze<button className="link link-dark">Expected Returns</button></div>
                            </div>
                          </div>
                        </div>
                      </div>{/* .card */}
                    </div>{/* .col */}
                  </div>{/* .row */}
                </div>{/* .nk-block */}
              </div>
            </div>
          </div>
        </div>
        {/* content @e */}
        <Footer />
      </div>
    );
  }
}

export default Dashboard


